<template>
  <f7-page>
    <f7-navbar ref="navbar">
      <f7-nav-left>
        <f7-link icon-f7="home" class="icon-white" v-on:click="goHome"></f7-link>
      </f7-nav-left>
      <f7-nav-title class="text-align-center">Scuba Test</f7-nav-title>
    </f7-navbar>

    <img class="center" :src="images.logoIcon">

    <f7-card v-if="test">
      <f7-card-header class="gray">
        <strong>{{ test.title }}</strong>
      </f7-card-header>
      <f7-card-content :padding="false">
        <f7-list v-for="(answer, index) in test.answers" :key="index">
          <f7-list-item link="#" v-on:click="answerQuestion(answer)">{{ answer }}</f7-list-item>
        </f7-list>
      </f7-card-content>
    </f7-card>

  </f7-page>
</template>
<script>
  import {main} from '../service/main';

  export default {
    data: function () {
      return {
        groupId: parseInt(this.$f7route.params.groupId),
        questionId: parseInt(this.$f7route.params.questionId),
        test: null,
        images: {
          logoIcon: require('../images/logo-icon.png')
        }
      }
    },
    mounted() {
      this.$f7ready(() => {
        this.getTest();
      });
    },
    methods: {
      getTest() {
        this.test = main.getTest(this.groupId, this.questionId);
      },

      answerQuestion(answer) {
        let correctAnswer = `<strong>Correct answer:</strong> ${this.test.answer}`;

        if (this.test.answer === answer) {
          main.updateScore(this.groupId, 'correct');
          this.$f7.dialog.alert(correctAnswer, '<span class="green">Correct</span>', () => this.goToNextQuestion());
        } else {
          main.updateScore(this.groupId, 'incorrect');
          this.$f7.dialog.alert(correctAnswer, '<span class="red">Incorrect</span>', () => this.goToNextQuestion());
        }
      },

      goToNextQuestion() {
        if (this.questionId + 1 >= 10) {
          this.goHome();
        } else {
          this.$f7.views.current.router.navigate(`/test/${this.groupId}/${this.questionId + 1}`);
        }
      },

      goHome() {
        this.$f7.views.current.router.navigate('/');
      }
    }
  }
</script>



