<template>
  <f7-app :params="f7params">
    <f7-statusbar></f7-statusbar>

    <f7-view id="main-view" url="/" main></f7-view>
  </f7-app>
</template>

<script>
  import routes from './routes.js'

  export default {
    data() {
      return {
        f7params: {
          id: 'com.massinflux.scubatests',
          name: 'ScubaTests',
          theme: 'auto',
          routes: routes,
        },
      }
    }
  }
</script>



