<template>
  <f7-page>
    <f7-navbar>
      <f7-nav-title>Scuba Tests</f7-nav-title>
      <f7-nav-right>
        <f7-link icon-if-ios="f7:info" icon-if-md="material:info" v-on:click="goToAboutPage"></f7-link>
      </f7-nav-right>
    </f7-navbar>

    <img class="center" :src="images.logoIcon">

    <f7-card v-if="tests" v-for="(test, index) in tests" :key="index">
      <f7-card-content :padding="false">
        <f7-row>
          <f7-col>
            <f7-list>
              <f7-list-item :title="test.name" :link="`/test/${index}/0`"></f7-list-item>
            </f7-list>
          </f7-col>
        </f7-row>
      </f7-card-content>
      <f7-card-content>
        <f7-row>
          <f7-col class="text-align-center green">Correct: {{ test.score.correct }}</f7-col>
          <f7-col class="text-align-center red">Incorrect: {{ test.score.incorrect }}</f7-col>
          <f7-col class="text-align-center">
            <f7-button small fill v-on:click="clearScore(index)">Clear</f7-button>
          </f7-col>
        </f7-row>
      </f7-card-content>
    </f7-card>
  </f7-page>
</template>
<script>
  import {main} from '../service/main';

  export default {
    data: function () {
      return {
        tests: null,
        images: {
          logoIcon: require('../images/logo-icon.png')
        }
      }
    },
    mounted() {
      this.$f7ready(() => {
        this.getTests();
      });
    },
    methods: {
      getTests() {
        this.tests = main.getTests();
        for (let i = 0; i < this.tests.length; i++) {
          this.tests[i].score = {
            correct: main.getScore(i, 'correct'),
            incorrect: main.getScore(i, 'incorrect'),
          };
        }
      },

      clearScore(groupId) {
        main.clearScore(groupId, 'correct');
        main.clearScore(groupId, 'incorrect');

        this.getTests();
      },

      goToAboutPage() {
        this.$f7.views.current.router.navigate('/about/');
      }
    }
  }
</script>



